import {
  Component,
  OnInit,
  ViewChildren,
  QueryList,
} from '@angular/core';
import {
  IonicPage,
  NavController,
  NavParams,
  ModalController,
  AlertController,
} from "ionic-angular";
import { ElementComponent } from '../../components/element/element.component';
import { RulesService } from '../../providers/rules.service';
import { RuleListService } from '../../providers/ruleList.service';
import { DatepickerOptions } from 'ng2-datepicker';
import { RuleListComponent } from '../rule-list/rule-list.component';


@IonicPage({
  name: "form",
  segment: "form/:rule-name"
})
@Component({
  selector: 'rule-form',
  templateUrl: './form.component.html'
})
export class FormComponent implements OnInit {

  @ViewChildren(ElementComponent) elements: QueryList<ElementComponent>;

  public options: DatepickerOptions = {
    displayFormat: 'MM/DD/YYYY',
    barTitleFormat: 'MMMM YYYY',
  };

  private tab: string = "rule";
  private showDropdown: boolean = false;
  private ruleCatagories: any = [
    {
      name: 'Purchase',
      value: 'P'
    },
    {
      name: 'Refinance',
      value: 'R'
    },
    {
      name: 'Others',
      value: 'O'
    },
  ];
  private ruleCatagory: any = {
    name: 'Refinance',
    value: 'R'
  };

  private pageTitle: string = "Create New Rule";
  private helpText: string = "Click anywhere inside the rule area to modify the rule.";

  private ruleName: string;
  private ruleDescription: string;
  private ruleLogic: any;
  private effectiveDate: any = new Date();
  private expiryDate: any = new Date();


  private formElems: any = [];
  private ruleString: string = "";
  private errorMessage: string = "";
  private exceptionStack: any = [];

  private ruleLogicArray: any = [];

  private mode = "text";

  private elementStack = [];

  private ruleListPage = RuleListComponent;

  constructor(
    public rulesService: RulesService, public ruleListService: RuleListService, public navCtrl: NavController,
    public navParams: NavParams, public alertCtrl: AlertController,
  ) { }

  ionViewWillLoad() {
    console.log(this.navParams.data)
    if (this.navParams.get('rule-name')) { // Edit view
      this.pageTitle = "Edit Rule";
      this.ruleName = this.navParams.get('rule-name');

      let rule = this.ruleListService.getRule(this.ruleName);
      this.ruleDescription = rule.rule_desc;
      this.ruleLogic = rule.rule_logic;
      this.effectiveDate = new Date(rule.effective_date);
      this.expiryDate = new Date(rule.expiry_date);
      this.ruleLogicArray = rule.rule_logic_array;
      console.log("form component..", this.ruleLogicArray)
    }
  }

  public ngOnInit() {
    this.elementStack = [];
    this.rulesService.rule.subscribe(res => {
      if (res.elementType === 'starter') {
        this.elementStack = [];
      }
      this.elementStack.push(res);
      this.constructResults();
    });
  }

  public constructResults() {

    this.formElems = [];
    this.exceptionStack = [];
    this.ruleString = "";

    let resultBlockFound = false;

    this.elementStack.forEach((elem, indx) => {

      if (elem.elementType == 'field' && !resultBlockFound) {

        let commandString = " #" + elem['value'];

        let operatorElem = this.elementStack[indx + 1];
        let valueElem = this.elementStack[indx + 2];

        if (!operatorElem || !valueElem) {
          return;
        }

        if (operatorElem['value'] && (operatorElem['value'] == "is" || operatorElem['value'] == "=")) {
          commandString += " == '" + valueElem['value'] + "' ";
        } else if (operatorElem['value'] && operatorElem['value'] == "isNot") {
          commandString += " != '" + valueElem['value'] + "' ";
        } else if (operatorElem['value'] && operatorElem['value'] == "contains") {
          commandString += ".indexOf('" + valueElem['value'] + "') > -1 ";
        } else if (operatorElem['value'] && operatorElem['value'] == "startsWith") {
          commandString += ".indexOf('" + valueElem['value'] + "') == 0 ";
        } else if (operatorElem['value'] && operatorElem['value'] == ">") {
          commandString += " > '" + valueElem['value'] + "' ";
        } else if (operatorElem['value'] && operatorElem['value'] == "<") {
          commandString += " < '" + valueElem['value'] + "' ";
        }

        if (!this.checkIfExists(elem, this.formElems)) {
          this.formElems.push(elem);
        }

        this.ruleString += commandString;
      }
      else if (elem.elementType == 'logicalOperator' && elem['value'] != "then") {
        this.ruleString += " " + elem['value'] + " ";
      }
      else if (elem.elementType == 'result' && elem.value == "addLoanException") {

        resultBlockFound = true;

        let exceptionMessageElem = this.elementStack[indx + 1];

        if (!exceptionMessageElem) {
          return;
        }

        this.errorMessage = exceptionMessageElem['value'];
      }
      else if (elem.elementType == 'result' && elem.value == "addRuleInputParameter") {

        resultBlockFound = true;

        let exceptionElem = this.elementStack[indx + 1];

        if (!exceptionElem) {
          return;
        }

        this.exceptionStack.push(exceptionElem);
      }
      else if (elem.elementType == 'paranthesis') {
        this.ruleString += " " + elem['name'] + " ";
      }

    });
  }

  private checkIfExists(obj, array) {
    let flag = false;
    array.forEach((elm) => {
      if (elm.value == obj.value) {
        flag = true;
      }
    });

    return flag;
  }

  public evaluateResults() {
    let message = "";
    let localRuleString = this.ruleString;

    this.elements.forEach(elementComponent => {

      let elmValObj = elementComponent.getValueObject();
      let replaceString = new RegExp(elmValObj.key, 'g');
      let replaceWith: string = elmValObj.value;

      localRuleString = localRuleString.replace(replaceString, replaceWith);

    });

    console.log("eval all elements", this.ruleString);
    let evalResult = eval(localRuleString);

    let exceptionListMessage = "<br><br> Attributes with exceptions are ";
    this.exceptionStack.forEach((elem, indx) => {
      exceptionListMessage += "<br>" + elem.name;
      if (indx != (this.exceptionStack.length - 1)) {
        exceptionListMessage += " , ";
      }
    });

    if (this.exceptionStack.length > 0) {
      this.errorMessage += exceptionListMessage;
    }

    if (!evalResult) {
      message = "No exceptions found in the test data!"
    } else {
      message = this.errorMessage;
    }

    console.log(message);
    

    let alert = this.alertCtrl.create({
      title: 'Test Results',
      subTitle: message,
      buttons: ['OK']
    });
    alert.present();

  }

  public getCleanRuleArray() {
    let localRuleArray = [];
    this.elementStack.map((elem) => {
      let rule = {
        "name": elem.name,
        "elementType": elem.elementType,
        "value": elem.value
      };

      if (elem['type']) {
        rule['type'] = elem['type'];
      }

      localRuleArray.push(rule);
    });

    return localRuleArray;
  }

  public saveRule() {
    console.log("Element Stack ::", this.elementStack, "Exception Stack ::", this.exceptionStack);
    let finalRule = {
      'rule_name': this.ruleName ? this.ruleName : '',
      'rule_category': this.ruleCatagory ? this.ruleCatagory : '',
      'rule_desc': this.ruleDescription ? this.ruleDescription : '',
      'rule_logic': this.ruleString,
      'rule_exception': this.errorMessage ? this.errorMessage : '',
      'effective_date': this.effectiveDate,
      'expiry_date': this.expiryDate,
      'last_modified': this.effectiveDate,
      'last_published': this.effectiveDate,
      'rule_author': 'Steve Octaviano',
      'rule_logic_array': this.getCleanRuleArray(),
    };

    console.log(JSON.stringify(finalRule, null, '\t'));

    if (this.pageTitle == "Edit Rule") {
      this.ruleListService.updateRule(finalRule);
    } else {
      this.ruleListService.saveRule(finalRule);
    }
  }

  public deleteRule() {
    if (window.confirm("Are you sure you want to delete this rule ?")) {
      console.log('deleting rule', this.ruleName);
      this.ruleListService.deleteRule(this.ruleName);
      this.navCtrl.pop();
    }
  }

  public changeEditorMode() {
    this.mode = this.mode === 'text' ? 'dropdown' : 'text';
  }

  public getRuleJSONString() {
    return JSON.stringify(this.getCleanRuleArray(), null, "  ");
  }

}



