import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

@Injectable()

export class LogicalOperatorsService {

  private logicalOperators = new BehaviorSubject<any>({
    'AND': {
      name: 'and',
      value: '&&',
    },

    'OR': {
      name: 'or',
      value: '||',
    },

    'THEN': {
      name: 'then',
      value: 'then',
    },

  });

  logicalOperator = this.logicalOperators.asObservable();

  constructor() { }

  changeOperator(logicalOperator) {
    this.logicalOperators.next(logicalOperator)
  }

}



