import {
  Component,
  OnInit,
  ViewChild,
  ElementRef,
  Input,
  HostListener,
} from '@angular/core';

import { FieldsService } from '../../providers/fields.service';
import { LogicalOperatorsService } from '../../providers/logical.service';
import { OperatorsService } from '../../providers/operators.service';
import { ResultsService } from '../../providers/result.service';

import { RulesService } from '../../providers/rules.service';

@Component({
  selector: 'rule-editor',
  templateUrl: './editor.component.html'
})
export class EditorComponent implements OnInit {

  // private ruleLogic: any = [
  //   {
  //     name: 'IF',
  //     elementType: 'starter',
  //     value: 'if',
  //   },
  // ];

  @Input('helpText') helpText: string;
  @Input('ruleLogic') ruleLogic: any = [];
  @Input('testRule') testRule: boolean;
  @Input('mode') mode: string = 'text';

  @ViewChild('dropDownElement') dropDownElement: ElementRef;
  @ViewChild('endElement') endElement: ElementRef;
  @ViewChild('ruleEditor') ruleEditor: ElementRef;


  @HostListener('document:click', ['$event']) clickedOutside($event){
    this.exitEditingMode($event);
  }

  public ruleEditMode = false;

  public fields = [];
  public logicalOperators = [];
  public operators = [];
  public values = [];
  public results = [];

  public dropDownTypes = [
    'fields',
    'operators',
    'values',
    'logicalOperators',
    'results',
  ];

  public dropDownType = 0;
  public dropDown = [];

  public fieldsLogic = {
    name: '',
    elementType: 'field',
    value: '',
  };
  public operatorsLogic = {
    name: '',
    elementType: 'operator',
    value: '',
  };
  public valuesLogic = {
    name: '',
    elementType: 'value',
    value: '',
  };
  public logicalOperatorsLogic = {
    name: '',
    elementType: 'logicalOperator',
    value: '',
  };
  public resultsLogic = {
    name: '',
    elementType: 'result',
    value: '',
  };
  public inputFieldsLogic = {
    name: '',
    elementType: 'inputField',
    value: '',
  };
  public messagesLogic = {
    name: '',
    elementType: 'value',
    value: '',
  };

  public currentFieldSelected = { type : '', options: []};
  public currentText = "";
  public dropDownSelectedIndex = -1;
  public customMode = false;
  public customString = '';
  public resultMode = false;

  public currentElement:any;

  public currentResult = { value: ''};
  public inputParameters = [];

  public openParanthesis = 0;

  public dropDownIndex = 0;
  public deletedElements = [];

  constructor(
    private fieldsService: FieldsService,
    private logicalOperatorsService: LogicalOperatorsService,
    private operatorsService: OperatorsService,
    private rulesService: RulesService,
    private resultsService: ResultsService,
  ) {}


  public ngOnInit() {
    this.fieldsService.field.subscribe(res => {
       this.fields = res; //.slice();
    });
    this.logicalOperatorsService.logicalOperator.subscribe(res => {
       this.logicalOperators = res; //.slice();
    });
    this.operatorsService.operator.subscribe(res => {
       this.operators = res; //.slice();
    });
    this.resultsService.result.subscribe(res => {
       this.results = res.slice();
    });
    // console .log(this.helpText);

    this.ruleLogic = this.ruleLogic.length ? this.ruleLogic : [
      {
        name: 'IF',
        elementType: 'starter',
        value: 'if',
      },
    ];

    if (this.ruleLogic.length > 1) {
      this.ruleLogic = this.addOptionsToRules(this.ruleLogic);
      this.addRulesToObservable(this.ruleLogic);
    }
  }

  public addOptionsToRules(ruleLogic) {
    let fields = this.fields;
    return ruleLogic.map((rule) => {
      if (rule.elementType === 'field' && rule.type === 'options') {
        let field = fields.find( (f) => {
          return f.value === rule.value;
        });
        rule.options = field.options;
      }
      return rule;
    })
  }

  public addRuleToObservable(rule) {
    // this.rulesService.addRule(rule);
  }

  public addRulesToObservable(rules) {
    if (!this.testRule) {
      return;
    }

    rules.forEach( (rule) => {
      this.rulesService.addRule(rule);
    });
  }

  public isNewLineRequired(logic) {
    if ((logic.elementType === 'logicalOperator' && logic.value != 'then') || logic.elementType === 'starter') {
      return true;
    }
    else {
      return false;
    }
  }

  public enterEditingMode(event) {

    if (!this.testRule) {
      return;
    }

    let element = event.srcElement;
    if (!this.ruleEditMode) {
      this.ruleEditMode = true;
      // if(this.ruleLogic[this.ruleLogic.length -1].elementType === 'value') {
      //   this.ruleLogic.pop();
      // }
      if(this.ruleLogic.length === 0 ) {
        this.ruleLogic.push({
          name: 'IF',
          elementType: 'starter',
          value: 'if',
        });
      }
      this.setDropdown({code: ''});
    }
    // $event.preventDefault();
    event.stopPropagation();
  }

  public setCurrentElement() {
    let ruleElements = [].slice.call(this.ruleEditor.nativeElement.children[0].children);
    let element = ruleElements[this.dropDownIndex];
    this.currentElement = element;
  }

  public exitEditingMode(event) {
    this.ruleEditMode = false;
    this.addRulesToObservable(this.ruleLogic);
  }

  public setNextDropDownType(result=false, exception=false, input=false) {
    this.dropDownType ++;

    if (!result && this.dropDownType >= this.dropDownTypes.length - 1 || this.dropDownType < 0) {
      this.dropDownType = 0;
    }
    if (result && this.dropDownType >= this.dropDownTypes.length ) {
      this.dropDownType = 0;
    }
    // if (exception || input ) {
    //   this.dropDownType = 1;
    // }
  }

  public emptyDropDown() {
    this.dropDown = [];
  }

  public rightArrow() {
    if (this.dropDownIndex < this.ruleLogic.length - 1) {
      this.dropDownIndex++;
      this.setCurrentElement();
      this.setDropDownPosOnEdit(this.currentElement);
      this.pushDropDownElements(this.dropDownIndex);
    }
  }

  public leftArrow() {
    // .log('leftArrow', this.dropDownIndex);
    if (this.dropDownIndex > 0) {
      this.dropDownIndex--;
      this.setCurrentElement();
      this.setDropDownPosOnEdit(this.currentElement);
      this.pushDropDownElements(this.dropDownIndex);
    }
  }

  public backspace(event) {
    event.preventDefault();
    if(this.ruleLogic[this.ruleLogic.length - 1].customMode
        || this.ruleLogic[this.dropDownIndex].customMode ) {
      this.setCustomString(this.customString.substring(0, this.customString.length-1));
      return;
    }
    if (this.currentText.length) {
      this.setCurrentText(this.currentText.substring(0,this.currentText.length-1))
      this.filterDropDown(this.currentText, true);
      return;
    }

    this.deleteRuleBlock(this.ruleLogic[this.dropDownIndex], this.dropDownIndex, this.ruleLogic)
  }

  public deleteRuleBlock(ruleElement, index, ruleLogic) {
    let elementType = ruleElement.elementType;
    let deleteStartIndex = 0;
    let deleteEndIndex = 0;
    let noOfElementsToDelete = 0;

    if ( elementType === 'field' || elementType === 'value') {
      elementType = (ruleLogic[index-1].elementType === 'result')
                      ? 'result-param'
                      : elementType;
    }

    switch (elementType) {
      case 'value' :
        deleteStartIndex = index - 2;
        deleteEndIndex = index + 1;
        noOfElementsToDelete = 4;
        break;

      case 'field' :
        deleteStartIndex = index;
        deleteEndIndex = index + 3;
        noOfElementsToDelete = 4;
        break;

      case 'operator' :
        deleteStartIndex = index - 2;
        deleteEndIndex = index + 1;
        noOfElementsToDelete = 4;
        break;

      case 'logicalOperator' :
        deleteStartIndex = index - 3;
        deleteEndIndex = index;
        noOfElementsToDelete = 4;
        break;

      case 'result' :
        deleteStartIndex = index;
        deleteEndIndex = index + 1;
        noOfElementsToDelete = 2;
        break;

      case 'result-param' :
        deleteStartIndex = index - 1;
        deleteEndIndex = index;
        noOfElementsToDelete = 2;
        break;
    }

      let deletedElements = this.ruleLogic.splice(deleteStartIndex, noOfElementsToDelete );
      this.deletedElements.push({elementsDeleted : deletedElements, index: deleteStartIndex});
      this.setDropdown({code: ''}, true);
  }

  undoDelete () {
    if(this.deletedElements.length) {
      let deletedElement = this.deletedElements.pop();
      this.ruleLogic.splice(deletedElement.index, 0, ...deletedElement.elementsDeleted );
    }
    this.setDropdown({code: ''}, true);
  }

  public setDropdown(event, empty=false) {
    let code = event.code;
    let key = event.key;
    if (empty) {
      this.emptyDropDown();
    }

    if (event.preventDefault) {
      event.preventDefault();
    }

    switch(code) {
      case '':
        this.pushLogic();
        this.pushDropDownElements();
        break;
      case 'Space':
        if(!this.customMode) {
          // this.dropDownSelectedIndex = 0;
          // this.enter();
          this.setCurrentText(this.currentText + key)
          this.filterDropDown(this.currentText);
        }
        if(this.ruleLogic[this.ruleLogic.length - 1].customMode) {
          this.setCustomString(this.customString + key);
        }
        break;
      case 'Backslash':
        this.setCurrentText(this.currentText.substr(0, this.currentText.length - 2));
        this.filterDropDown(this.currentText);
        break;
      case 'Enter':
        if(this.customMode) {
          this.exitCustomMode();
          this.setCustomValueInRule(this.customString);
        } else {
          this.dropDownSelectedIndex = 0;
          this.enter();
        }
        break;
      default:
        if(this.ruleLogic[this.ruleLogic.length - 1].customMode) {
          this.setCustomString(this.customString + key);
          return;
        }
        this.setCurrentText(this.currentText + key)
        this.filterDropDown(this.currentText);
        break;
    }
    let base = this;
    setTimeout(() => {
      base.setDropDownPos(base);
    }, 20);

  }

  public setEndOfContenteditable(event) {
    let contentEditableElement = event.srcElement;
    var range,selection;
    if(document.createRange)
    {
        range = document.createRange();
        range.selectNodeContents(contentEditableElement);
        range.collapse(false);
        selection = window.getSelection();
        selection.removeAllRanges();
        selection.addRange(range);
    }
  }

  public filterDropDown(text, backspace?) {
    let dropDown = this.filterList(this.dropDown, text);
    this.dropDown = dropDown;

    if(backspace) {
      this.pushDropDownElements();
      let dropDown = this.filterList(this.dropDown, text);
      this.dropDown = dropDown;
    }

    if(dropDown.length < 1) {
      this.emptyCurrentText();
      this.pushDropDownElements();
    }
  }

  public filterList(list, text) {
    return list.filter((l) => {
      // return l.name.toUpperCase().includes(text.toUpperCase());
      return l.name.toUpperCase().startsWith(text.toUpperCase());
    });
  }

  public pushLogic() {
    this.dropDownIndex++;
    if (this.dropDownIndex > this.ruleLogic.length) {
      this.dropDownIndex = this.ruleLogic.length-1;
    }
    this.setCurrentElement();
    let logic = this.ruleLogic[this.dropDownIndex]
      ? this.ruleLogic[this.dropDownIndex]
      : this[this.dropDownTypes[this.dropDownType] + 'Logic'];
    let dropDownType = logic.elementType + 's';
    this.ruleLogic.push(this[dropDownType + 'Logic']);
  }

  public getStringFields() {
    return this.fields.filter((f) => {
      return f.type === 'string';
    });
  }

  public getNumberFields() {
    return this.fields.filter((f) => {
      return f.type === 'number';
    });
  }

  public pushDropDownElements(dropDownIndex?) {

    this.emptyDropDown();


    let logic = this.ruleLogic[this.dropDownIndex] ? this.ruleLogic[this.dropDownIndex] : this[this.dropDownTypes[this.dropDownType] + 'Logic'];
    let dropDownType = logic.elementType + 's';

    // let dropDownType = this.dropDownTypes[this.dropDownType];

    if (this.ruleLogic[dropDownIndex] === undefined) {
      // console .log('undefiend rule logic')
    }

    if (dropDownIndex != undefined) {
      dropDownType = this.ruleLogic[dropDownIndex].elementType + 's';
    }

    if (this['get' + dropDownType] === undefined) {
      return;
    }

    let dropDownElements = this['get' + dropDownType]();

    if (dropDownType === 'fields') {
      this.dropDown.push(this.getOpenParanthesis());
    }
    else if (dropDownType === 'logicalOperators' && this.openParanthesis > 0) {
      this.dropDown.push(this.getCloseParanthesis());
    }

    this.dropDown.push(...dropDownElements);
  }

  public getOpenParanthesis() {
    return {
      name: '(',
      value: 'OPEN_PARANTHESIS',
      elementType: 'paranthesis',
    }
  }

  public getCloseParanthesis() {
    return {
      name: ')',
      value: 'CLOSE_PARANTHESIS',
      elementType: 'paranthesis',
    }
  }

  public getfields() {
    return this.fields;
  }
  public getlogicalOperators() {
    return Object.keys(this.logicalOperators).map((k) => {
      return this.logicalOperators[k]
    });
  }
  public getoperators() {
    let fieldType = this.currentFieldSelected.type;
    if (this.dropDownIndex < this.ruleLogic.length - 1 && this.ruleLogic[this.dropDownIndex - 1]!= undefined) {
      fieldType = this.ruleLogic[this.dropDownIndex - 1].type;
    }
    return this.operators[fieldType];
  }
  public getvalues() {
    let dropdown = [];

    if (this.resultMode) {
      dropdown.push(...this.getEnterAStringObject());
      return dropdown;
    }

    let fieldType = this.currentFieldSelected.type;
    if (this.dropDownIndex < this.ruleLogic.length - 1 && this.ruleLogic[this.dropDownIndex - 2] != undefined) {
      fieldType = this.ruleLogic[this.dropDownIndex - 2].type;
    }

    switch(fieldType) {

      case 'number':
        dropdown.push({
          name: 'Enter a number',
          value: 'CUSTOM',
          custom: true,
        });
        dropdown.push(...this.getNumberFields());
        return dropdown;

      case 'string':
        dropdown = this.getEnterAStringObject();
        dropdown.push(...this.getStringFields());
        return dropdown;

      case 'options':
        if (this.dropDownIndex === this.ruleLogic.length - 1) {
          return this.currentFieldSelected.options;
        }
        else {
          return this.ruleLogic[this.dropDownIndex - 2].options;
        }

    }
  }
  public getresults() {
    return this.results;
  }
  public getmessages() {
    return this.getEnterAStringObject();
  }
  public getEnterAStringObject() {
    return [{
      name: 'Enter a string',
      value: 'CUSTOM',
      custom: true,
    }];
  }
  public getinputFields() {
    return this.inputParameters;
  }

  public upArrow(event) {
    this.dropDownSelectedIndex--;
    this.dropDownSelectedIndex = this.dropDownSelectedIndex < 0
                                  ? 0
                                  : this.dropDownSelectedIndex;
  }

  public downArrow(event) {
    this.dropDownSelectedIndex++;
    this.dropDownSelectedIndex = this.dropDownSelectedIndex >= this.dropDown.length
                                  ? this.dropDown.length
                                  : this.dropDownSelectedIndex;
  }

  public enter(event?) {
    event && event.preventDefault ? event.preventDefault() : {};
    let selectedDropDown = this.dropDown[this.dropDownSelectedIndex];
    this.selectOption(selectedDropDown);
    this.dropDownSelectedIndex = -1;
  }

  public setDropDownPos(base) {
    base = base ? base : this;

    if ( this.dropDownIndex != this.ruleLogic.length - 1 && this.currentElement != undefined ) {
      this.setDropDownPosOnEdit(this.currentElement);
      return;
    }
    let dropDown = this.dropDownElement;
    let endElement = this.endElement;

    let ruleEditor = this.ruleEditor;

    let offsetLeft = ruleEditor.nativeElement.offsetLeft;
    let offsetTop = ruleEditor.nativeElement.offsetTop;

    let fontHeight = parseFloat(getComputedStyle(endElement.nativeElement).fontSize);

    let x = endElement.nativeElement.offsetTop + (fontHeight * 2);
    let y = endElement.nativeElement.offsetLeft;

    dropDown.nativeElement.style.top =  x + 'px';
    dropDown.nativeElement.style.left =  y + 'px';
  }

  public setDropDownPosOnEdit(element) {
    let base = this;
    let dropDown = this.dropDownElement;

    let ruleEditor = this.ruleEditor;

    let offsetLeft = ruleEditor.nativeElement.offsetLeft;
    let offsetTop = ruleEditor.nativeElement.offsetTop;

    let fontHeight = parseFloat(getComputedStyle(element).fontSize);

    let x = element.offsetTop + (fontHeight * 2);
    let y = element.offsetLeft;

    dropDown.nativeElement.style.top =  x + 'px';
    dropDown.nativeElement.style.left =  y + 'px';

  }

  public selectOption(dropDownEle) {
    let ruleIndex = this.dropDownIndex;
    this.dropDownSelectedIndex = -1;

    let move = true;
    if (this.dropDownIndex < this.ruleLogic.length - 1) {
      move = false;
    }

    if (dropDownEle.value === 'OPEN_PARANTHESIS' || dropDownEle.value === 'CLOSE_PARANTHESIS') {
      this.ruleLogic.splice(this.ruleLogic.length - 1, 0, dropDownEle);
      this.addRuleToObservable(this.ruleLogic[this.ruleLogic.length - 2]);
      this.dropDownType = 0;
      if (dropDownEle.value === 'CLOSE_PARANTHESIS') {
        this.dropDownType = -1;
        move = false;
      }
      dropDownEle.value === 'OPEN_PARANTHESIS' ? this.openParanthesis++ : this.openParanthesis--;
      this.moveToNextElement();
      return;
    }

    let ruleLogic = this.ruleLogic[ruleIndex];

    this.ruleLogic[ruleIndex] = Object.assign({}, ruleLogic, dropDownEle);

    ruleLogic = this.ruleLogic[ruleIndex];

    if(ruleLogic.elementType === 'field' && !this.resultMode) {
      let inputParamIndex = this.inputParameters.findIndex(i => i.value===ruleLogic.value);
      if (inputParamIndex < 0) {
        this.inputParameters.push(dropDownEle);
      }
      this.currentFieldSelected = ruleLogic;
    }

    this.emptyCurrentText();
    this.emptyDropDown();

    if (dropDownEle.custom) {
      this.setCustomOption();
      return;
    }

    if (this.resultMode) {
      this.currentResult = ruleLogic;
      if(ruleLogic.value === 'addLoanException') {
        this.results.splice(0,1);
        this.askCustomMessage();
      }
      if(ruleLogic.value === 'addRuleInputParameter') {
        this.setInputParametersInDropDown();
      }
    }


    if (ruleLogic.elementType === 'field' && this.resultMode) {
      let inputParamIndex = this.inputParameters.findIndex(i => i.value === dropDownEle.value);
      this.inputParameters.splice(inputParamIndex, 1);
      this.addRuleToObservable(this.ruleLogic[this.ruleLogic.length - 1]);

      if (this.inputParameters.length === 0) {
        let resultIndex = this.results.findIndex(i => i.value === 'addRuleInputParameter');
        this.results.splice(resultIndex, 1);
      }

      let resultExceptionIndex = this.results.findIndex(i => i.value === 'addLoanException');

      if (this.inputParameters.length || resultExceptionIndex >= 0 ) {
        this.moveToNextElement(true);
      }

      // end of rule logic is reached
      if(this.inputParameters.length === 0 && resultExceptionIndex < 0) {
        console.log('End of Rule Logic..', this.ruleLogic);
      }

      this.addRulesToObservable(this.ruleLogic);
      return;
    }

    if (ruleLogic.elementType === 'logicalOperator' && ruleLogic.name === 'then') {
      this.addRuleToObservable(this.ruleLogic[ruleIndex]);
      this.moveToResult();
      return;
    }
    this.addRuleToObservable(this.ruleLogic[ruleIndex]);

    if (this.dropDownIndex === this.ruleLogic.length - 2 ) {
      this.dropDownIndex = this.ruleLogic.length - 1;
    }


    this.moveToNextElement(this.resultMode, move);

  }

  public setCustomOption() {
    this.setFocusToRuleEditor();
    this.enterCustomMode();
    this.ruleLogic[this.ruleLogic.length - 1].value = '';
  }

  public setCustomString(text) {
    this.customString = text;
  }
  public enterCustomMode() {
    this.ruleLogic[this.ruleLogic.length - 1].customMode = true;
    this.customMode = true;
  }

  public exitCustomMode() {
    this.ruleLogic[this.ruleLogic.length - 1].customMode = false;
    this.customMode = false;
  }

  public setCustomValueInRule(customString) {
    this.ruleLogic[this.ruleLogic.length - 1].value = customString;
    this.addRuleToObservable(this.ruleLogic[this.ruleLogic.length - 1]);
    this.setCustomString('');
    if(this.resultMode) {
      this.addRulesToObservable(this.ruleLogic);
    }
    this.moveToNextElement(this.resultMode);
  }

  public moveToNextElement(result=false, move=true) {
    if (move) {
      this.setNextDropDownType(result);
    }

    this.setDropdown({code: ''}, true);
    this.setFocusToRuleEditor();
  }

  public moveToResult() {
    this.resultMode = true;
    this.moveToNextElement(true);
  }

  public askCustomMessage() {
    this.dropDownType = -1;
    this.dropDownTypes = ['messages', 'results']
  }

  public setInputParametersInDropDown() {
    this.dropDownType = -1;
    this.dropDownTypes = ['inputFields', 'results'];
  }

  public emptyCurrentText() {
    this.currentText = '';
  }

  public setCurrentText(text) {
    this.currentText = text;
  }

  public setFocusToRuleEditor() {
    this.setFocusToElement(this.ruleEditor.nativeElement);
    this.dropDownSelectedIndex = -1;
  }

  public setFocusToDropDown() {
    this.setFocusToElement(this.dropDownElement.nativeElement);
    this.dropDownSelectedIndex++;
  }

  public setFocusToElement(element) {
    var p = element,
    s = window.getSelection(),
    r = document.createRange();
    r.setStart(p, 0);
    r.setEnd(p, 0);
    s.removeAllRanges();
    s.addRange(r);
  }

  public prevent(event) {
    event.preventDefault();
    event.stopPropagation();
  }

  public logicClicked(event, logic, index) {

    this.dropDownIndex = index;
    if (logic.value === 'then' || logic.elementType === 'result') {
      this.emptyDropDown();
      return;
    }
    this.setDropDownPosOnEdit(event.target);
    this.pushDropDownElements(this.dropDownIndex);
  }

  public getClasses(logic) {
    switch (logic.elementType) {
      case 'field' :
        return 'col-2';
      case 'operator' :
        return 'col-3';
      case 'value' :
        return 'col-4';
      case 'logicalOperator' :
        return 'col-6';
    }
  }

  public getOptions(logic, index) {
    this.dropDownIndex = index;
    let options = this['get'+logic.elementType+'s']();
    return options;
  }

  public logicSelectChange(logic, index, event) {
    let [name,value] = event.target.value.split(',');
    if(value === undefined){
      logic.value = name;
      this.ruleLogic[index] = logic;
      return;
    }
    logic.name = name;
    logic.value = value;
    this.ruleLogic[index] = logic;
  }

}



