import {
  Component,
  OnInit,
  Input
} from '@angular/core';

@Component({
  selector: 'element',
  templateUrl: './element.component.html'
})
export class ElementComponent implements OnInit {

  @Input() elementObject: any;
  private showDropdown = false;
  private selectedValue = "";

  constructor() {}

  public ngOnInit() {
    this.selectedValue = this.elementObject.type === 'options' ? this.elementObject.options[0] : '';
  }

  public getValueObject() {

    let key = "#"+this.elementObject['value'];
    let value = this.selectedValue;
    
    if (typeof(this.selectedValue) == "object" ) {
      value = value['value'];
    }

    return { 'key' : key, 'value' : "'"+value+"'" };
  }

  public numberRestrict(evt) {
    let theEvent = evt || window.event;
    let key = theEvent.keyCode || theEvent.which;
    key = String.fromCharCode( key );
    let regex = /[0-9]|\./;
    if( !regex.test(key) ) {
      theEvent.returnValue = false;
      if(theEvent.preventDefault) theEvent.preventDefault();
    }
  }
}



