import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

@Injectable()

export class RulesService {

  private rules = new BehaviorSubject<any>([
    {
      name: 'IF',
      elementType: 'starter',
      newline: true,
      dropDown: false,
      value: 'if'
    }
  ]);

  rule = this.rules.asObservable();

  constructor() { }

  changeRule(rule, index) {
    this.rules.next(rule)
  }

  addRule(rule) {
    this.rules.next(rule)
  }

}



